#spin-model{position:fixed;left:20px;top:60px;background:#fff;z-index:99;border-radius:3px}#spin-model .svg-box{width:100px;height:66px;position:absolute;left:50%;top:50%;margin-left:-50px;margin-top:-33px;text-align:center}#spin-model .svg-box .sp1{display:block;font-size:12px;color:#999;padding-top:4px}#spin-model.spin-sp1{width:calc(100% - 40px);height:calc(100% - 60px)}#spin-model.spin-sp2{width:calc(100% - 220px);height:calc(100% - 60px);left:220px}